.Card {
  width: 500px;
  text-align: center;
  background-color: #eeeeee;
  padding: 1rem;
  box-shadow: 6px 6px 14px 0 rgba(0, 0, 0, 0.2),
    -8px -8px 18px 0 rgba(255, 255, 255, 0.55);
  border-radius: 40px;
}

.Card img {
  width: 30%;
}

.Card button {
  display: block;
  margin: 1rem auto;
}
